import { createApp } from "vue";
import App from "./App.vue";
import "./main.css";
import * as vueRouter from "vue-router";
import ShoppingCartPage from "./pages/shopping-cart.vue";
import ProductsPage from "./pages/products.vue";
import ProductDetailsPage from "./pages/product-details.vue";
import NotFoundPage from "./pages/not-found-page.vue";

// Import the functions you need from the SDKs you need
import { initializeApp } from "firebase/app";
// TODO: Add SDKs for Firebase products that you want to use
// https://firebase.google.com/docs/web/setup#available-libraries

// Your web app's Firebase configuration
const firebaseConfig = {
  apiKey: "AIzaSyDwbm-Y81-NvwgOe3n9Id9w-20jUNsKmrk",
  authDomain: "vue-site-14db3.firebaseapp.com",
  projectId: "vue-site-14db3",
  storageBucket: "vue-site-14db3.firebasestorage.app",
  messagingSenderId: "768820047155",
  appId: "1:768820047155:web:a67e7fa741e0b13b56a246",
};

// Initialize Firebase
initializeApp(firebaseConfig);

createApp(App)
  .use(
    vueRouter.createRouter({
      history: vueRouter.createWebHistory(process.env.BASE_URL),
      routes: [
        {
          path: "/cart",
          component: ShoppingCartPage,
        },
        {
          path: "/products",
          component: ProductsPage,
        },
        {
          path: "/products/:productId",
          component: ProductDetailsPage,
        },
        {
          path: "/:pathMatch(.*)*",
          component: NotFoundPage,
        },
        {
          path: "/",
          redirect: "/products",
        },
      ],
    })
  )
  .mount("#app");
