<template>
  <div v-if="product">
    <div class="img-wrap">
      <img :src="product.imageUrl" :alt="product.name" />
    </div>
    <div class="product-details">
      <h1>{{ product.name }}</h1>
      <h3 class="price">{{ product.price }}</h3>
      <button
        @click="addToCart"
        class="add-to-cart"
        :class="{ 'grey-button': isItemInCart }"
        :disabled="isItemInCart"
      >
        {{ isItemInCart ? "Item already in cart" : "Add to Cart" }}
      </button>
    </div>
  </div>
  <div v-else>
    <NotFoundPage />
  </div>
</template>

<script>
import NotFoundPage from "./not-found-page.vue";
import axios from "axios";
export default {
  name: "ProductDetailsPage",
  data() {
    return {
      product: {},
      cartItems: [],
    };
  },
  components: {
    NotFoundPage,
  },
  methods: {
    async addToCart() {
      await axios.post("/api/users/12345/cart", {
        id: this.$route.params.productId,
      });
      this.cartItems.push({ id: this.$route.params.productId });
      alert("Successfully added item to cart!");
    },
  },
  computed: {
    isItemInCart() {
      return this.cartItems.some(
        (item) => item.id === this.$route.params.productId
      );
    },
  },
  async created() {
    const response = await axios.get(
      `/api/products/${this.$route.params.productId}`
    );
    const product = response.data;
    this.product = product;

    const cartResponse = await axios.get(`/api/users/12345/cart`);
    const cartItems = cartResponse.data;
    this.cartItems = cartItems;
  },
};
</script>
